@import url('https://webfontworld.github.io/goodchoice/Jalnan.css');
body{
  font-family: 'Jalnan';
  color:#464646;
  font-weight: 500;
  background-image: url(/public/images/background.jpg);
  background-repeat: no-repeat;
}
.WeatherWrap{
background: rgba( 255, 255, 255, 0.1 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 2px );
-webkit-backdrop-filter: blur( 2px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
  /* background-image: url(/public/images/bg1.png); */
  /* background-size: cover; */
  width: 600px;
  height: 470px;
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 20px;
  line-height: 1rem;
}
.WeatherBox_inner{
  margin-top: 10px;
  border-radius: 20px;
  padding: 2rem;
  color: white;
background-color: lightblue;
  text-align: center;
}
.location_point{
  text-align: left;
  margin-top: 0;
  font-size: 1.5rem;
}
.temper{
  margin-right: -30px;
  font-size: 70px;
  color: white;
}
.weather_name{
  font-size: 18px;
}
.weatherIcon{
  position: absolute;
  margin-left: 340px;
  margin-top: -55px;
  z-index: 20;
}

.WeatherButton{
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
}

button{
  border-radius: 50px;
  padding: 1rem 1.8rem;
  cursor: pointer;
  color:black;
  font-family: 'Jalnan';
  font-size: 0.85rem;
  border: 1px solid lightgray;
  background-color: rgb(238, 238, 238);
  box-shadow: 2px 2px 2px gray;
  transition: 0.25s;
}
button:hover{
  box-shadow: 2px 2px 2px black;
  background-color:grey;
  color:#fff;
}
/* 위치조정수정해야됨 */
.ClipLoader{
  position: relative;
  top: 400px;
  left: 50%;
  transform: translateY(-50%);
  z-index: 5;
}
@media screen and (max-width: 780px) {
  .WeatherWrap{
    width: 300px;
    height: 540px;
    border-radius: 30px;
  }
  .WeatherBox_inner{
    margin-top: -7px;
    font-size: 0.65rem;
    padding: 0.8rem;}

  .WeatherButton{
    margin-top: 20px;
    flex-direction: column;
    gap: 6px;
    justify-content: space-around;
  }
  button{
    border-radius: 50px;
    padding: 0.7rem;
    font-size: 0.8rem;
  }
  .weatherIcon{
    position: absolute;
    margin-left: 30px;
    margin-top: -22px;
    z-index: 20;
  }
  .location_point{
    font-size: 1rem;
  }
  .weather_name{
    font-size: 0px;
  }
  
  .temper{
    margin-top: 130px;
    margin-right: -30px;
    font-size: 40px;
    color: white;
  }
  .ClipLoader{
    top: 300px;
    left: 40%;
  }
}